<template>
  <div class="tiles" :class="[ this.name.startsWith('Tiles/Number') ? tiles.Flashing ? 'flash' : '' : '', ]" :style="cssProps" :title="'Состояние элемента ' + tiles.tooltip" v-show="tiles.Visible" >
    <p :id="this.name" v-show="tiles.value!='text'">{{ tiles.value }}</p>
  </div>
</template>
<script>
export default {
  name: 'Tile',
  props: {
    params:{}, 
    name:{}, 
    subscreensize:{},
    screenPercentage:{},
    windowWidth:{},
    windowHeight:{},
    type:{}
  },
  data() {
    return {
      tiles: {
        Name: '',
        value: '',
        ForegroundColor: '',
        BackgroundColor: '',
        Flashing: false,
        AlarmInfo: '',
        Visible: true,
        Enabled: true,
        tooltip: '',
      },
    };
  },
  created(){
    this.tiles.Name = this.name,
    this.tiles.value = this.params.text,
    this.tiles.ForegroundColor = this.params.foreground,
    this.tiles.BackgroundColor = this.params.background,
    this.tiles.Flashing = false,
    this.tiles.AlarmInfo = "Good",
    this.tiles.Visible = this.params.visible,
    this.tiles.Enabled = true,
    this.tiles.tooltip = "подсказка!"
    if( this.type.startsWith("tiles/Number") || (this.$parent.typewindow == 'head' )) {
      setInterval(() => {
        let changedelem = this.$store.getters.elemByName(this.tiles.Name)?.properties
        if (changedelem) {
          if (changedelem.text) this.tiles.value = changedelem.text
          if (changedelem.background) this.tiles.BackgroundColor = changedelem.background
          if (changedelem.foreground) this.tiles.ForegroundColor = changedelem.foreground
        }
      },1000)
    }
  },
  computed: {
    cssProps() {
      return {
        "--x": this.params.x * this.subscreensize * this.$parent.multiplier + "px",
        "--y": this.params.y * this.subscreensize * this.$parent.multiplier + "px",
        "--z": this.params.z,
        "--width": this.params.width * this.subscreensize * this.$parent.multiplier + "px",
        "--height": this.params.height * this.subscreensize * this.$parent.multiplier  + "px",
        "--backgroundColor": "#" + [ this.tiles.BackgroundColor == "Blue" ? "#373737" : this.tiles.BackgroundColor, ],
        "--color": "#" + this.tiles.ForegroundColor,
        "--borderThickness": this.params.borderThickness * this.subscreensize * this.$parent.multiplier + "px",
        "--borderBrush": "#" + this.params.borderBrush,
        "--scale": this.params.scale,
        "--align": this.params.hAlignment,
        "--fontSize": [this.params.fontSize == 0 ? 14 / 1 : this.params.fontSize / 1] * this.subscreensize * this.$parent.multiplier + "px",
        "--margin": this.params.margin.split(" : ")[0] + "px",
        "--borderRadius": this.params.borderRadius * this.$parent.multiplier + "px"
      };
    },
  },
};
</script>
<style scoped>
.tiles {
  display: flex;
  transform: scale(var(--scale));
  position: absolute;
  left: var(--x);
  top: var(--y);
  width: var(--width);
  height: var(--height);
  background-color: var(--backgroundColor);
  color: var(--color);
  border-radius: var(--borderRadius);
  border-color: var(--borderBrush);
  border-width: var(--borderThickness);
  border-Radius: var(--borderRadius);
  border-style: solid;
  justify-content: flex-start;
  margin-left: var(--margin);
}
p {
  margin: auto; 
  width: 100%;
  text-align: var(--align);
  font-size: var(--fontSize);
}

@keyframes glowing {
  from {
    background-color: var(--backgroundColor);
  }
  50% {
    background-color: var(--backgroundColor);
    filter: brightness(50%);
  }
  to {
    background-color: var(--backgroundColor);
  }
}
.flash {
  animation: glowing 2000ms infinite;
}
</style>
