<template>
  <div class="button" :style="cssProps" v-show="button.Visible" @click="some">
    <p>{{ button.value }}</p>
  </div>
</template>

<script>
export default {
  name: "app",
  props: ["params", "windowWidth", "subscreensize"],
  data() {
    return {
      button: {
        Name: "button",
        value: this.params.text,
        ForegroundColor: this.params.foreground,
        BackgroundColor: this.params.background,
        AlarmInfo: "Good",
        Visible: true,
        Enabled: true,
      },
    };
  },
  methods: {
    some(){
      let data = []
      data.name = this.params.path
      data.title = this.params.text
      data.screenPercentage = this.params.screenPercentage
      this.$store.dispatch('addElems', data)
    }
  },
  computed: {
    cssProps() {
      return {
        "--x": this.params.x / 1 * this.subscreensize * this.$parent.multiplier + "px",
        "--y": this.params.y / 1 * this.subscreensize * this.$parent.multiplier + "px",
        "--z": this.params.z,
        "--width": this.params.width / 1 * this.subscreensize * this.$parent.multiplier + "px",
        "--height": this.params.height / 1 * this.subscreensize * this.$parent.multiplier + "px",
        "--backgroundColor": [ this.params.neightbourState == "Red" ? 'Red' : [this.params.neightbourState == "Yellow" ? "Yellow" : '#' + this.button.BackgroundColor]],
        "--color": [this.params.neightbourState == "Yellow" ? "Black" : this.button.ForegroundColor],
        "--borderThickness": this.params.borderThickness,
        "--borderBrush": this.params.borderBrush,
        "--scale": this.params.scale,
        "--borderRadius": this.params.borderRadius,
        "--fontSize": this.params.fontSize / 1 * this.subscreensize * this.$parent.multiplier + "px",
      };
    },
  },
};
</script>

<style scoped>
.button {
  user-select: none;
  -ms-user: none;
  position: absolute;
  left: var(--x);
  top: var(--y);
  width: var(--width);
  height: var(--height);
  background-color: var(--backgroundColor);
  color: var(--color);
  display: flex;
  border-radius: var(--borderRadius);
  border-color: white;
  border-width: 1px;
  border-style: solid;
  text-align: center;
  font-size: var(--fontSize);
}

p {
  margin: auto;
  text-align: center;
}

.button:hover {
  background-color: var(--backgroundColor);
  filter: brightness(80%);
  background-blend-mode: da;
}
.button:active {
  background-color: var(--backgroundColor);
  filter: brightness(150%);
}
</style>
